              <div style="position:relative;overflow:hidden;height:100%;">
                <img :src="$utils.dealImg(item.bimg)" alt style="position:absolute;left:50%;top:50%;transform:translate(-50%,-50%);min-width:100%;min-height:100%;"/>
              </div>
            </div>
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="top">
        <div class="flex-vertical" style="height:40px;margin-top:10px;padding:0 16px;background-color:#fffbe8;">
