      <div class="flex-between" style="padding:24px 10px;">
        <router-link to="/signIn" class="flex-column flex-between flex-vertical" style="width: 20%;padding:4px 0;">
          <img src="@/assets/img/icon/sign_in.png" alt="" style="height: 30px;margin-bottom:4px;">
          <p style="font-size:12px;color:#666;">签到</p>
        </router-link>
        <div class="flex-column flex-between flex-vertical" style="width: 20%;padding:4px 0;" @click="_open_camera">
          <img src="@/assets/img/icon/scan_code.png" alt="" style="height: 30px;margin-bottom:4px;">
          <p style="font-size:12px;color:#666;">扫一扫</p>
        </div>
        <router-link to="/recommend" class="flex-column flex-between flex-vertical" style="width: 20%;padding:4px 0;">
          <img src="@/assets/img/icon/index_share.png" alt="" style="height: 30px;margin-bottom:4px;">
          <p style="font-size:12px;color:#666;">分享</p>
        </router-link>
        <router-link :to="{ path: '/bonus', query: { code: '1202763465547927552', name: '红包' } }" class="flex-column flex-between flex-vertical" style="width: 20%;padding:4px 0;">
          <img src="@/assets/img/icon/red_envelopes.png" alt="" style="height: 30px;margin-bottom:4px;">
          <p style="font-size:12px;color:#666;">红包</p>
        </router-link>
      </div>

      <div class="mid">

        <!-- 会员必抢 -->
        <div class="goods-list-header flex-horizontal">
          <div class="flex-vertical" style="height:100%;">
            <img src="@/assets/img/icon/text_adorn.png" alt="" style="height: 6px;filter:blur(1px);">
            <span style="margin:0 8px;">会员必抢</span>
            <img src="@/assets/img/icon/text_adorn.png" alt="" style="height: 6px;filter:blur(1px);">
          </div>
        </div>
        <div class="list">
          <div class="item" v-for="(item,index) in goodsList1" :key="index" @click="goDetail(item)">
