            </div>
          </div>
          <div v-if="goodsList1.length === 0" style="text-align:center;font-size:12px;color:#999;width:100%;">没有数据</div>
        </div>

        <!-- 为您推荐 -->
        <div class="goods-list-header flex-horizontal">
          <div class="flex-vertical" style="height:100%;">
            <img src="@/assets/img/icon/text_adorn.png" alt="" style="height: 6px;filter:blur(1px);">
            <span style="margin:0 8px;">为您推荐</span>
            <img src="@/assets/img/icon/text_adorn.png" alt="" style="height: 6px;filter:blur(1px);">
          </div>
        </div>
        <van-tabs v-model="active" :line-width="20" :line-height="3" :swipe-threshold="6"  :title-active-color="'#071ff7'" @click="onClick">
          <van-tab
            v-for="(item,index) in goodsClassifyList"
            :key="index"
            :title="item.cateName"
            title-style="color:#666;"
          ></van-tab>
        </van-tabs>
        <div class="list">
          <div class="item" v-for="(item,index) in goodsList2" :key="index" @click="goDetail(item)">
            <div class="topImage flex-center">
            <img :src="$utils.dealImg(item.gimg)" alt />
            </div>
            <div class="bottomText">
              <p class="title">{{item.gname}}</p>
              <div class="labels">
                <div class="label" v-if="item.gfanj">送{{item.gfanj}}积分</div>
              </div>
              <div class="bottom">
                <div class="left">
                   <div class="price1">
                    <span class="label">优惠价</span>
                    <span class="font">¥</span>
                    <span class="num">{{item.goutPrice?item.goutPrice.toFixed(2):'0.00'}}</span>
                  </div>
                  <div class="price">
                    <span class="label">市场价</span>
                    <span class="font">¥</span>
                    <span class="num">
                      <del>{{item.gmarketPrice?item.gmarketPrice.toFixed(2):'0.00'}}</del>
                    </span>
                  </div>
                 </div>
              </div>
            </div>
          </div>
          <div v-if="goodsList2.length === 0" style="text-align:center;font-size:12px;color:#999;width:100%;">没有数据</div>
        </div>
      <div class="line" style="border-bottom:0"></div> 

    </div>
 
    <van-popup v-model="show">
      <div class="popup-mid" @click="show = false">
        <img :src="qrcode" alt />
      </div>
    </van-popup>
  </div>
  </div>
</template>
<script>
import { Tab, Tabs, NoticeBar, Search, Swipe, SwipeItem, Popup, Panel  } from "vant";
import MeScroll from "mescroll.js";
import {
  getBanner,
  getIndexMenu,
  getIndexGoodsList,
  getShopNavigation,
  getPersonalInfo,
  getFilgain,
  getArticleList
} from "@/api";
import Grid from "@/components/Grid";
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [NoticeBar.name]: NoticeBar,
    [Search.name]: Search,
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Popup.name]: Popup,
    Grid,
    Panel
  },
  data() {
    return {
      classifyList: [],
      goodsClassifyList: [], // 二级分类
      picList: [],
      value: "",
      active: 0,
      goodsList1: [], // 会员必抢
      goodsList2: [], // 为您推荐
      show: false,
      qrcode: "",
      roofShow: true,
      userLevel: 1,
      filgainList: [],
      news: null,
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    bannerClick(item) {
      console.log(item);
      if (this.userLevel > 2) {
        this.$router.push({
          name: "guandanRegister",
        });
      } else {
        this.$router.push({
          name: "guandanRegister",
        });
      }
    },
    creatCanvas() {
      var that = this;
      var wid1 = 900 * 0.29; //大 右
      var hei1 = 1100 * 0.37; //大 下
      var canvas = document.createElement("canvas");
      var ctx = canvas.getContext("2d");
      var ratio = this.getPixelRatio(ctx) * 0.5;
      canvas.width = 750 * ratio;
      canvas.height = 1100 * ratio;
      ctx.rect(0, 0, canvas.width * ratio, canvas.height * ratio);
      ctx.fillStyle = "#fff";
      ctx.fill();
      var img = new Image();
      img.src = require("../../assets/img/bg.jpg");
      img.crossOrigin = "Anonymous";
      img.onload = function() {
        ctx.drawImage(img, 0, 0, canvas.width, canvas.height);
        var img1 = new Image();
        img1.src = that.$config.qrCode;
        img1.onload = function() {
          ctx.drawImage(
            img1,
            (canvas.width - 400 * ratio) / 2,
            (canvas.height - 450 * ratio) / 2,
            400 * ratio,
            400 * ratio
          );
          ctx.fillStyle = "#fff";
          ctx.font = "3.5em normal 黑体";
          ctx.textAlign = "center";
          ctx.fillText(
            "长按识别图中二维码，关注公众号",
            canvas.width / 2,
            (canvas.height - 450 * ratio) / 2 + 435 * ratio
          );
          // var base64 = canvas.toDataURL("image/jpeg", 1); //"image/png" 这里注意一下,参数里面不要带"image/png" 之前网上代码还有这个参数一直报错
          // that.qrcode = base64;
        };
      };
    },
    circleImg(ctx, img, w, h, x, y) {
      ctx.save();
      ctx.beginPath(); //开始绘制
      ctx.arc(w / 2 + x, h / 2 + y, w / 2, 0, Math.PI * 2, false);
      ctx.clip(); //画好了圆 剪切  原始画布中剪切任意形状和尺寸。一旦剪切了某个区域，则所有之后的绘图都会被限制在被剪切的区域内 这也是我们要save上下文的原因
      ctx.drawImage(img, x, y, w, h); // 推进去图片，必须是https图片
      ctx.restore(); //恢复之前保存的绘图上下文 恢复之前保存的绘图上下午即状态 还可以继续绘制
    },
    getPixelRatio(context) {
      var backingStore =
        context.backingStorePixelRatio ||
        context.webkitBackingStorePixelRatio ||
        context.mozBackingStorePixelRatio ||
        context.msBackingStorePixelRatio ||
        context.oBackingStorePixelRatio ||
        context.backingStorePixelRatio ||
        1;
      return (window.devicePixelRatio || 1) / backingStore;
    },
    goRouter(item) {
      this.$router.push({
        name: "goodsList",
        query: { id: item.id },
      });
    },
    onSearch() {
      this.loadData();
    },
    onClick() {
      this.value = "";
      this.loadData();
    },
    goDetail(item) {
      this.$router.push({
        name: "goodsDetail",
        query: { id: item.gcode, title: item.gname },
      });
    },
    async getData() {
      let that = this;
      getPersonalInfo().then(res => {
        if (res.code == 0) {
          this.userLevel = parseInt(res.data.userLevel);
        } else {
          this.$toast.fail(res.msg);
        }
      });
      getFilgain().then(res => {
        if (res.code == 0) {
           this.filgainList = res.data;
        } else {
          this.$toast.fail(res.msg);
        } 
      }); 
      getBanner().then(res => {
        if (res.code == 0) {
          this.picList = res.data;
        } else {
          this.$toast.fail(res.msg);
        }
      });
      
      // 获取商城的二级分类
      let res = await getIndexMenu({
        cateCode: '825678A88DCD43528E3268E2F65A045D',
        cateLevel: 2
      })
      if (res.code == 0) {
        this.goodsClassifyList = res.data;
        this.loadData();
      } else {
        this.$toast.fail(res.msg);
      }
    },
    loadData() {
      let that = this;

      // 获取会员必抢产品
      getIndexGoodsList({
        cateCode: '1206576824790290432',
        viewName: this.value,
        pageNum: 1,
        pageSize: 10000,
      }).then(res => {
        if (res.code == 0) {
          that.goodsList1 = res.data.list;
        } else {
          $this.$toast.fail(res.errorMsg);
        }
      })
      
      // 获取为您推荐产品
      getIndexGoodsList({
        cateCode: this.goodsClassifyList[this.active].cateCode,
        viewName: this.value,
        pageNum: 1,
        pageSize: 10000,
      }).then(res => {
        if (res.code == 0) {
          that.goodsList2 = res.data.list;
        } else {
          $this.$toast.fail(res.errorMsg);
        }
      })

      // 获取农场头条新闻
      getArticleList({
        cateCode: '362D3BB8CEE14CC99479C09398F6C4E4',
        key: '',
        pageNum: 1,
        pageSize: 1
      }).then(res=>{
        if(res.code === 0){
          this.news = res.data.list[0]
        }else{
          this.$toast.fail(res.msg);
        }
      })
    },
    _open_camera(){
      this.$router.push('/barCode');
    }
  },
  mounted() {
    document.title = this.$config.name;
    this.getData();
    this.creatCanvas();
  },
};
</script>
